.RegisterPage {
    height: 100vh;
    width: 100%;
    background: #f6eedb;
    color: #927682;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}
.RegisterPage h1 {
    font-size: 50px;
}
.underLine {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    cursor: default;
}
.underLine div {
    height: 2px;
}
.underLine:hover div {
    background: #927682;
    -webkit-animation: stretch 0.5s linear;
            animation: stretch 0.5s linear;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}
@-webkit-keyframes stretch {
    0% {
        height: 2px;
        width: 0px;
    }
    50% {
        height: 2px;
        width: 50%;
    }
    100% {
        height: 2px;
        width: 100%;
    }
}
@keyframes stretch {
    0% {
        height: 2px;
        width: 0px;
    }
    50% {
        height: 2px;
        width: 50%;
    }
    100% {
        height: 2px;
        width: 100%;
    }
}


